import React from 'react';
import { FiArrowRight, FiLoader, FiCheck, FiX, FiClock } from 'react-icons/fi';
import { motion } from 'framer-motion';

interface ToolCallsProps {
  toolCalls: any[];
  onToolCallClick: (toolCall: any) => void;
  getToolIcon: (name: string) => React.ReactNode;
  isIntermediate?: boolean;
  toolResults?: any[]; // Add toolResults to check completion status
}

/**
 * Component for displaying tool calls with loading states and status icons
 *
 * Design principles:
 * - Shows loading state for pending tool calls
 * - Displays success/error status with appropriate icons
 * - Maintains compact display for thinking sequences
 * - Provides clear visual feedback for tool execution status
 * - Enhanced colors for better visibility while keeping simplicity
 */
export const ToolCalls: React.FC<ToolCallsProps> = ({
  toolCalls,
  onToolCallClick,
  getToolIcon,
  isIntermediate = false,
  toolResults = [],
}) => {
  // Helper function to get tool call status
  const getToolCallStatus = (toolCall: any) => {
    const result = toolResults.find((result) => result.toolCallId === toolCall.id);
    
    if (!result) {
      return 'pending'; // No result yet, tool is still running
    }
    
    if (result.error) {
      return 'error'; // Tool execution failed
    }
    
    return 'success'; // Tool completed successfully
  };

  // Helper function to get status icon
  const getStatusIcon = (status: string) => {
    switch (status) {
      case 'pending':
        return (
          <motion.div
            animate={{ rotate: 360 }}
            transition={{ duration: 1, repeat: Infinity, ease: 'linear' }}
          >
            <FiLoader size={10} className="text-blue-600 dark:text-blue-400" />
          </motion.div>
        );
      case 'success':
        return <FiCheck size={10} className="text-green-600 dark:text-green-400" />;
      case 'error':
        return <FiX size={10} className="text-red-600 dark:text-red-400" />;
      default:
        return <FiClock size={10} className="text-orange-500 dark:text-orange-400" />;
    }
  };

  // Helper function to get status color classes with enhanced visibility
  const getStatusColorClasses = (status: string) => {
    switch (status) {
      case 'pending':
        return 'border-blue-300 dark:border-blue-600 bg-gradient-to-r from-blue-50 to-blue-100 dark:from-blue-900/30 dark:to-blue-800/20 text-blue-800 dark:text-blue-200 shadow-sm';
      case 'success':
        return 'border-green-300 dark:border-green-600 bg-gradient-to-r from-green-50 to-green-100 dark:from-green-900/30 dark:to-green-800/20 text-green-800 dark:text-green-200 shadow-sm';
      case 'error':
        return 'border-red-300 dark:border-red-600 bg-gradient-to-r from-red-50 to-red-100 dark:from-red-900/30 dark:to-red-800/20 text-red-800 dark:text-red-200 shadow-sm';
      default:
        return 'border-orange-300 dark:border-orange-600 bg-gradient-to-r from-orange-50 to-orange-100 dark:from-orange-900/30 dark:to-orange-800/20 text-orange-800 dark:text-orange-200 shadow-sm';
    }
  };

  // Helper function to get hover effect classes
  const getHoverColorClasses = (status: string) => {
    switch (status) {
      case 'pending':
        return 'hover:from-blue-100 hover:to-blue-150 dark:hover:from-blue-800/40 dark:hover:to-blue-700/30 hover:border-blue-400 dark:hover:border-blue-500';
      case 'success':
        return 'hover:from-green-100 hover:to-green-150 dark:hover:from-green-800/40 dark:hover:to-green-700/30 hover:border-green-400 dark:hover:border-green-500';
      case 'error':
        return 'hover:from-red-100 hover:to-red-150 dark:hover:from-red-800/40 dark:hover:to-red-700/30 hover:border-red-400 dark:hover:border-red-500';
      default:
        return 'hover:from-orange-100 hover:to-orange-150 dark:hover:from-orange-800/40 dark:hover:to-orange-700/30 hover:border-orange-400 dark:hover:border-orange-500';
    }
  };

  return (
    <div className="mt-2 space-y-1.5">
      {toolCalls.map((toolCall) => {
        const status = getToolCallStatus(toolCall);
        const statusColorClasses = getStatusColorClasses(status);
        const hoverColorClasses = getHoverColorClasses(status);

        return (
          <motion.button
            key={toolCall.id}
            onClick={() => onToolCallClick(toolCall)}
            className={`flex items-center gap-2 px-3 py-1.5 text-xs font-medium rounded-2xl transition-all duration-200 hover:scale-[1.02] active:scale-[0.98] border text-left group w-full ${statusColorClasses} ${hoverColorClasses}`}
            initial={{ opacity: 0, y: 5 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.2 }}
            whileHover={{ 
              y: -1,
              transition: { duration: 0.15 }
            }}
          >
            {/* Tool icon with enhanced visibility */}
            <div className="flex-shrink-0 opacity-90">
              {getToolIcon(toolCall.function.name)}
            </div>

            {/* Tool name with better contrast */}
            <div className="truncate flex-1 font-semibold">{toolCall.function.name}</div>

            {/* Status and arrow with enhanced styling */}
            <div className="flex items-center gap-1.5 flex-shrink-0">
              {/* Status icon */}
              <div className="flex items-center justify-center">
                {getStatusIcon(status)}
              </div>

              {/* Arrow - only show if completed */}
              {status !== 'pending' && (
                <FiArrowRight
                  className="opacity-70 group-hover:opacity-100 group-hover:translate-x-0.5 transition-all duration-200"
                  size={11}
                />
              )}
            </div>
          </motion.button>
        );
      })}
    </div>
  );
};
